<template>
  <section>
    <SearchContainer>
      <template #inputSearch>
        <input
          v-model="query"
          :theme="theme"
          type="search"
          class="form-control rounded-3 ps-5 p-2"
          :placeholder="placeholder"
        />
      </template>
    </SearchContainer>
    <TransitionGroup
      tag="ul"
      :css="false"
      @before-enter="onBeforeEnter"
      @enter="onEnter"
      @leave="onLeave"
      :theme="theme"
      name="list"
      class="p-0 m-0 w-100"
    >
      <li
        v-for="(userNewChat, index) in usersNewChat"
        :key="userNewChat.id"
        style="list-style-type: none; width: 100%"
        :data-index="index"
      >
        <UserNewChatVue
          data-bs-dismiss="offcanvas"
          aria-label="Close"
          :user="userNewChat"
          @click="clickUserConversation(userNewChat)"
        />
        <!-- <UserNewChatVue
          v-else
          :user="userNewChat"
          @click="clickUserConversation(userNewChat.id)"
        /> -->
      </li>
    </TransitionGroup>
  </section>
</template>
<script>
import gsap from "gsap";
import { mapMutations } from "vuex";
import UserNewChatVue from "./UserNewChat.vue";
import SearchContainer from "./SearchContainer.vue";
import { makeDatabaseUsersRepository } from "@/firebase/database-user-repository";
export default {
  data() {
    return {
      query: "",
      changeIcon: false,
      placeholder: "Search name or email",
      listUsers: [],
    };
  },
  components: { UserNewChatVue, SearchContainer },
  computed: {
    usersNewChat() {
      return this.listUsers.filter((item) =>
        item.name.toLowerCase().includes(this.query.toLocaleLowerCase())
      );
    },
    theme() {
      return this.$store.state.theme;
    },
    profile() {
      return this.$store.state.profile;
    },
  },
  methods: {
    ...mapMutations(["setDataConversations", "setDataListMessages"]),
    
     clickUserConversation(conversations) {
      this.setDataConversations(conversations);
      this.setDataListMessages([]);
    },
    onBeforeEnter(el) {
      el.style.opacity = 0;
      el.style.height = 0;
    },
    onEnter(el, done) {
      gsap.to(el, {
        opacity: 1,
        height: "auto",
        delay: el.dataset.index * 0.15,
        onComplete: done,
      });
    },
    onLeave(el, done) {
      gsap.to(el, {
        opacity: 0,
        height: 0,
        delay: el.dataset.index * 0.15,
        onComplete: done,
      });
    },
  },
  async mounted() {
    makeDatabaseUsersRepository().snapshotDatalist((list) => {
      this.listUsers = list;
    }, this.profile.email);
  },
};
</script>
<style scoped>
.form-control {
  border: none;
  padding-left: 40px;
  color: var(--matte-font-color);
  background-color: var(--header-background);
}
.form-control:focus {
  box-shadow: none;
}
.list-move {
  transition: all 0.5s ease;
}
.form-control::placeholder {
  color: var(--matte-font-color);
}
.form-control[theme="dark"] {
  background-color: var(--dark-header);
}
ul[theme="dark"] {
  background-color: var(--dark-background);
}
input:focus.form-control::placeholder {
  opacity: 0;
}
</style>
