<template>
  <div
    :theme="theme"
    @focusin="focusDiv()"
    @focusout="focusDiv()"
    class="d-flex p-2 px-3 align-items-center sticky-top z-2"
  >
    <span
      id="iconSearch"
      class="btn position-absolute rounded-circle border-0"
      style="font-size: 12px; color: var(--matte-font-color); left: 20px"
      ><i
        class="fa-solid"
        :class="{
          'fa-arrow-right': changeIcon,
          'fa-magnifying-glass': !changeIcon,
        }"
      ></i
    ></span>
    <slot name="inputSearch"> </slot>
  </div>
</template>

<script>
export default {
  name: "searchContainer",
  data() {
    return {
      changeIcon: false,
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
  },
  methods: {
    focusDiv() {
      this.changeIcon = !this.changeIcon;
    },
  },
};
</script>

<style scoped>
div[theme="dark"] {
  background: var(--dark-background);
  border-bottom: solid var(--dark-header);
}
div {
  background: white;
  border-bottom: solid var(--header-background);
}

.fa-arrow-right {
  transform: rotate(180deg);
  color: var(--green-color);
  font-size: 16px;
}
input,
.fa-magnifying-glass,
.fa-arrow-right {
  transition: 0.3s;
}
.fa-arrow-right {
  animation-name: opacity;
  animation-duration: 0.3s;
}
@keyframes opacity {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
input:focus.form-control::placeholder {
  opacity: 0;
}
</style>
