<template>
  <ModePopup :title="'Confirm the operation'" :id="'updateImageProfileModal'">
    <template #body
      ><p class="fs-5">Do you want to change your profile picture?</p></template
    >
    <template #footer>
      <label for="file-upload" class="custom-file-upload">
        <span
          data-bs-dismiss="modal"
          class="btn px-4 fw-semibold rounded-5 text-white"
          style="background: var(--green-header); font-size: 14px"
          >changing</span
        >
      </label>
      <input
        id="file-upload"
        accept="image/jpeg"
        @change="previewFiles"
        type="file"
        class="d-none"
      />
      <button
        class="btn px-4 fw-semibold rounded-5 border-light-subtle"
        style="color: var(--green-header); background: white; font-size: 14px"
        data-bs-dismiss="modal"
      >
        close
      </button>
    </template>
  </ModePopup>
  <OffcanvasContent
    class="OffcanvasContent text-center"
    :id="'offcanvasProfile'"
    :title="'profile'"
    :classIcon="'fa-solid fa-arrow-left'"
  >
    <div
      class="content overflow-hidden position-relative h-100"
      style="user-select: none"
    >
      <div
        class="imgUser w-100 z-3 position-relative d-flex align-items-center justify-content-center flex-column"
      >
        <img
          v-if="dataProfile.imgUser"
          class="rounded-circle mt-4 mb-4"
          :src="dataProfile.imgUser"
        />
        <i
          v-else
          class="fa-solid fa-user mt-5 mb-5 text-secondary"
          style="font-size: 150px"
        ></i>
        <div
          class="imgUserHover position-absolute rounded-circle d-flex align-items-center justify-content-center flex-column"
          style="
            width: 200px;
            height: 200px;
            background: rgba(50, 50, 50, 0.468);
            cursor: pointer;
          "
          data-bs-toggle="modal"
          data-bs-target="#updateImageProfileModal"
        >
          <span class="fs-2 text-white"
            ><i class="fa-solid fa-camera"></i
          ></span>
          <p class="text-uppercase text-white fs-6" style="width: 85%">
            change profile photo
          </p>
        </div>
      </div>
      <div :theme="theme" class="nameUser w-100 p-3 px-4 text-start shadow">
        <span style="color: var(--matte-font-color); font-size: 14px"
          >Your name</span
        >
        <div
          class="position-relative w-100 mt-2 pt-2 d-flex justify-content-between"
        >
          <span>{{ dataProfile.name }}</span>
          <input
            type="text"
            :class="{ showInputUpate: showInputUpate }"
            v-model="dataProfile.name"
            @focusin="shwoProfile"
            class="hideInput form-control m-auto p-2 position-absolute w-75 mx-auto p-0 rounded-0 bottom-0"
            style="border-bottom: solid var(--green-color)"
            minlength="3"
            maxlength="20"
          />
          <span
            class="upName position-absolute end-0"
            @click="clickUpdateName"
            style="
              color: var(--matte-font-color);
              cursor: pointer;
              min-height: 50px;
            "
            ><i
              class="fa-solid"
              :class="{
                'fa-check': showInputUpate,
                'fa-pencil': !showInputUpate,
              }"
            >
            </i
          ></span>
        </div>
      </div>
      <div class="mx-auto mt-2 mb-2" style="width: 80%">
        <span style="font-size: 14px; color: var(--matte-font-color)"
          >This is not your username or pin. This name will be visible to your
          WhatsApp contacts.</span
        >
      </div>
      <div
        class="profileAbout mt-2 w-100 p-2 px-4 text-start shadow"
        :theme="theme"
      >
        <span style="color: var(--matte-font-color); font-size: 14px"
          >About</span
        >
        <div
          class="w-100 position-relative mt-2 pt-2 text-capitalize d-flex justify-content-between"
          style="min-height: 50px"
        >
          <span v-if="!showInputUpateAbout"
            >{{ dataProfile.about.slice(0, 40) }}
            <span v-if="dataProfile.about.length > 40">...</span></span
          >
          <span
            class="aboutUser position-absolute end-0 z-3"
            @click="clickUpdateAbout"
            style="color: var(--matte-font-color); cursor: pointer"
            ><i
              class="fa-solid"
              :class="{
                'fa-check': showInputUpateAbout,
                'fa-pencil': !showInputUpateAbout,
              }"
            ></i
          ></span>
          <input
            type="text"
            :class="{ showInputUpate: showInputUpateAbout }"
            v-model="dataProfile.about"
            class="hideInput form-control m-auto p-2 position-absolute w-75 mx-auto p-0 rounded-0 bottom-0"
            style="border-bottom: solid var(--green-color)"
            ref="inputAbout"
          />
        </div>
      </div>
      <button
        data-bs-toggle="modal"
        data-bs-target="#signOutModel"
        class="btn mt-2 btn-danger w-100 rounded-0 position-absolute start-0"
        style="bottom: 10px"
      >
        SignOut
      </button>
    </div>
  </OffcanvasContent>
  <ModePopup :title="'Confirm the operation'" :id="'signOutModel'">
    <template #body> <p class="fs-5">Do you want to go out?</p> </template>
    <template #footer>
      <button
        @click="onSignOut"
        style="font-size: 14px"
        class="btn btn-danger px-4 text-white fw-semibold rounded-5 border-light-subtle"
        data-bs-dismiss="modal"
      >
        confirmation
      </button>
      <button
        class="btn px-4 text-white fw-semibold rounded-5 border-light-subtle"
        style="background: var(--green-header); font-size: 14px"
        data-bs-dismiss="modal"
      >
        close
      </button>
    </template>
  </ModePopup>
</template>

<script>
import { mapMutations } from "vuex";
import OffcanvasContent from "./OffcanvasContainer.vue";
import ModePopup from "@/components/ModePopup.vue";
import auth from "@/firebase/authentiocation.js";
import { makeDatabaseUsersRepository } from "@/firebase/database-user-repository";
export default {
  name: "profile",
  data() {
    return {
      showInputUpate: false,
      showInputUpateAbout: false,
      nameUser: "",
      aboutUser: "",
    };
  },
  components: { OffcanvasContent, ModePopup },
  computed: {
    dataProfile() {
      return this.$store.state.profile;
    },
    theme() {
      return this.$store.state.theme;
    },
  },
  methods: {
    ...mapMutations(["setDataProfile", "setDataConversations", ""]),
    clickUpdateName() {
      this.showInputUpate = !this.showInputUpate;
      if (!this.showInputUpate) {
        makeDatabaseUsersRepository().updateById(this.dataProfile.id, {
          name: this.dataProfile.name.toLowerCase(),
        });
      }
    },
    clickUpdateAbout() {
      this.showInputUpateAbout = !this.showInputUpateAbout;
      if (!this.showInputUpateAbout) {
        makeDatabaseUsersRepository().updateById(this.dataProfile.id, {
          about: this.dataProfile.about,
        });
      }
    },
    previewFiles(e) {
      const image = e.target.files[0];
      const reader = new FileReader();
      if (image) {
        reader.readAsDataURL(image);
        reader.onload = async (e) => {
          this.dataProfile.imgUser = e.target.result;
          await makeDatabaseUsersRepository().updateById(this.dataProfile.id, {
            imgUser: e.target.result,
          });
        };
      }
    },
    shwoProfile() {
      setTimeout(() => {
        const divPro = document.querySelector(".OffcanvasContent");
        divPro.classList.add("show");
      }, 300);
    },
    async onSignOut() {
      const us = await auth.signOutUser();
      this.setDataProfile({});
      this.setDataConversations(null);
      this.$router.push({ name: "Login" });
      alert(us);
    },
  },
};
</script>

<style scoped>
.form-control {
  border: none;
  padding-left: 40px;
  color: var(--matte-font-color);
}
.OffcanvasContent {
  background: var(--app-background);
}
.OffcanvasContent[theme="dark"] {
  background: var(--dark-background);
}

.nameUser {
  background: white;
}
.form-control:focus {
  box-shadow: none;
}
.hideInput {
  display: none;
}
.showInputUpate {
  display: block;
}
img {
  object-fit: cover;
  width: 180px;
  height: 180px;
  opacity: 0;
}
.imgUserHover {
  opacity: 0;
}
.imgUserHover:hover {
  opacity: 1;
}
.content {
  opacity: 0;
  margin: auto;
}
.profileAbout {
  background: white;
}
.profileAbout[theme="dark"],
.profileAbout[theme="dark"] .form-control,
.nameUser[theme="dark"],
.nameUser[theme="dark"] .form-control {
  background: var(--dark-header);
  color: white;
}
.show img {
  animation-name: sizeImg;
  animation-delay: 0.2s;
  animation-duration: 0.7s;
  animation-fill-mode: both;
}
.show .content {
  animation-name: conten;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}
@keyframes sizeImg {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1.1);
  }
}
@keyframes conten {
  0% {
    opacity: 0;
    translate: 0px -100px;
  }
  100% {
    opacity: 1;
    translate: 0px 0px;
    height: auto;
  }
}
</style>
// col-xxl-4 col-xl-4 col-lg-5 col-md-5 col-sm-7 col-10
