<script>
import gsap from "gsap";
import UserConversation from "./UserConversation.vue";
import SearchContainer from "./SearchContainer.vue";
import { makeDatabaseChatsRepository } from "@/firebase/database-chats-repository";
export default {
  props: {
    list: Array,
  },
  data() {
    return {
      query: "",
      changeIcon: false,
      placeholder: "Search or start new chat",
      listC: [],
    };
  },
  components: { UserConversation, SearchContainer },
  computed: {
    computedList() {
      return this.listChats.filter((item) => {
        item.idChat.toLowerCase().includes(this.query.toLowerCase());
      });
    },
    theme() {
      return this.$store.state.theme;
    },
    profile() {
      return this.$store.state.profile;
    },
  },
   mounted() {
    makeDatabaseChatsRepository(this.profile.id).snapshotListChats(
      (list) => (this.listC = list)
    );
  },
  methods: {
    onBeforeEnter(el) {
      el.style.opacity = 0;
      el.style.height = 0;
    },
    onEnter(el, done) {
      gsap.to(el, {
        opacity: 1,
        height: "auto",
        delay: el.dataset.index * 0.15,
        onComplete: done,
      });
    },
    onLeave(el, done) {
      gsap.to(el, {
        opacity: 0,
        height: 0,
        delay: el.dataset.index * 0.15,
        onComplete: done,
      });
    },
  },
};
</script>

<template>
  <SearchContainer :changeIcon="changeIcon">
    <template #inputSearch>
      <input
        v-model="query"
        type="search"
        :theme="theme"
        class="form-control rounded-3 ps-5 p-2"
        :placeholder="placeholder"
      />
    </template>
  </SearchContainer>
  <TransitionGroup
    tag="ul"
    :css="false"
    @before-enter="onBeforeEnter"
    @enter="onEnter"
    @leave="onLeave"
    :theme="theme"
    name="list"
    class="list p-0 w-100 m-0 oveflow-y-auto"
  >
    <li
      v-for="(item, index) in listC"
      :key="item.idChat"
      style="list-style-type: none; width: 100%"
      :data-index="index"
    >
      <!-- {{ listChats[index].name }} -->
      <UserConversation v-if="item" :userConversation="item" :index="index" />
    </li>
  </TransitionGroup>
</template>
<style scoped>
.form-control {
  border: none;
  color: var(--matte-font-color);
  background-color: var(--header-background);
}
.form-control[theme="dark"] {
  background-color: var(--dark-header);
}
.form-control::placeholder {
  color: var(--matte-font-color);
}
.form-control:focus {
  box-shadow: none;
}
.list-move {
  transition: all 0.5s ease;
}
.list[theme="dark"] {
  background-color: var(--dark-background);
}

.list {
  background-color: white;
  min-height: calc(100% - 59px);
}
  
  input:focus.form-control::placeholder{
    opacity: 0;
  }
</style>
