<template>
  <a
    v-if="listMessages.length != 0 && dataUserComputed"
    :theme="theme"
    href="#scroll-of-last-Message"
    @click="onClickChat()"
    class="conversation position-relative rounded-0 text-start btn border-0 mb-1 w-100 d-flex align-items-center"
  >
    <UserImg :srcU="dataUserComputed.imgUser" />
    <div
      class="flex-column position-relative mt-auto p-2 d-flex flex-column justify-content-center"
    >
      <span class="nameUser" style="text-transform: capitalize">{{
        dataUserComputed.name
      }}</span>
      <span
        class="position-absolute text-lowercase end-0"
        style="font-size: 14px; color: var(--matte-font-color)"
        v-if="!userConversation.block && !userConversation.heBlock"
      >
        {{ timeEndMessage }}
        <p ref="text" class="text-end" style="font-size: 10px"></p>
      </span>
      <p
        v-if="lastMessageChatting"
        style="font-size: 15px; color: var(--matte-font-color)"
      >
        <span v-if="lastMessageChatting.interaction.you"
          >You reacted{{ lastMessageChatting.interaction.you }} to:</span
        >
        <span v-else-if="lastMessageChatting.interaction.he"
          >He reacted{{ lastMessageChatting.interaction.he }} to:</span
        >
        {{ lastMessageChatting.message.slice(0, 6) }}
        <span v-if="checkLengthMessage"> ...</span>
      </p>
    </div>
    <span class="badge text-bg-danger" v-if="userConversation.block"
      >Blocked</span
    >
    <Transition name="fade">
      <span
        v-if="
          newMessages.length != 0 &&
          this.idConversations != this.userConversation.idFrinde
        "
        style="opacity: 0.6;"
        class="newMessage p-1 rounded-circle text-white position-absolute d-flex align-items-center justify-content-center end-0 me-1"
      >
        {{ newMessages.length }}
      </span>
    </Transition>
    <audio ref="audio" controls class="d-none">
      <source src="../../assets/iphone_14_notification.mp3" type="audio/mp3" />
    </audio>
  </a>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import UserImg from "../headerMain/UserImg.vue";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
import { makeDatabaseUsersRepository } from "@/firebase/database-user-repository";
export default {
  name: "userConversation",
  props: {
    userConversation: {
      createdAt: Number,
      idChat: String,
      idChatFrinde: String,
      idFrinde: String,
      block: Boolean,
      heBlock: Boolean,
    },
    index: Number,
  },
  data() {
    return {
      dataUser: null,
      listMessages: [],
      newMessages: [],
    };
  },
  components: {
    UserImg,
  },
  methods: {
    ...mapMutations([
      "setDataConversations",
      "setDataChat",
      "setDataReplayMessage",
      "setDataListMessages",
    ]),
    getRepositoryMessage() {
      return makeDatabaseMessagesRepository(
        this.profile.id,
        this.userConversation.idChat
      );
    },
    getRepositoryMessageFrinde() {
      return makeDatabaseMessagesRepository(
        this.userConversation.idFrinde,
        this.userConversation.idChatFrinde
      );
    },
    async NewListUnreadMessages() {
      this.getRepositoryMessageFrinde().snapshotDataNewMessages((list) => {
        this.newMessages = list;
      });
    },
    async SnapshotListMessagesCollection() {
      this.getRepositoryMessage().snapshotDataMessages((list) => {
        this.listMessages = list;
        if (this.idConversations === this.userConversation.idFrinde) {
          // this.$store.state.listMessagesChat = list;
          this.getRepositoryMessageFrinde().viewMessages(this.newMessages);
          this.setDataListMessages(list);
        }
      });
    },
    async onClickChat() {
      this.setDataConversations(this.dataUser);
      this.setDataChat(this.userConversation);
      this.setDataReplayMessage(null);
      this.setDataListMessages(this.listMessages);
      this.getRepositoryMessageFrinde().viewMessages(this.newMessages);
    },
    async snapshotDataUser() {
      makeDatabaseUsersRepository().snapshotDataUserById(
        this.userConversation.idFrinde,
        (obj) => {
          this.dataUser = obj;
        }
      );
    },
    async audioNewMessages() {
      try {
        const audio = this.$refs.audio;
        if (this.idConversations != this.userConversation.idFrinde)
        await audio.play();
      } catch (err) {
        console.error(err.message);
      }
    },
  },
  // TODO:
  computed: {
    ...mapState(["theme", "profile", "conversations", "detailsChat"]),
    idConversations() {
      if (this.conversations) return this.conversations.id;
    },
    lastMessageChatting() {
      if (this.listMessages)
        return this.listMessages[this.listMessages.length - 1];
      return { id: "" };
    },
    timeEndMessage() {
      return new Date(this.userConversation.lastTime).toLocaleTimeString([], {
        hour: "2-digit",
        minute: "2-digit",
      });
    },
    checkLengthMessage() {
      return this.listMessages.length > 16;
    },
    dataUserComputed() {
      if (this.listMessages.length != 0)
        makeDatabaseUsersRepository().snapshotDataUserById(
          this.userConversation.idFrinde,
          (data) => {
            !this.userConversation.heBlock
              ? (this.dataUser = data)
              : (this.dataUser.name = data.name);
          }
        );
      return this.dataUser;
    },
  },
  async mounted() {
    // TODO:erorr new Chat
    // if(this.detailsChat.idChat){
    this.SnapshotListMessagesCollection();
    this.NewListUnreadMessages();
    // }
  },
  watch: {
    userConversation: {
      handler() {
        // if (this.idConversations === this.userConversation.idFrinde)
        //   this.setDataChat(this.userConversation);
      },
    },
    newMessages: {
      async handler() {
        // TODO:error
        if (this.newMessages.length > 0) {
          this.audioNewMessages();
        }
      },
    },
  },
};
</script>

<style scoped>
.newand {
  opacity: 1;
}

img {
  width: 50px;
  height: 50px;
  object-fit: cover;
}
.conversation .flex-column {
  width: calc(100% - 70px);
  height: 100%;
  border-bottom: solid 1px var(--header-background);
}
.conversation[theme="dark"] .flex-column {
  border-bottom: solid 1px var(--dark-header);
}
.conversation:hover,
.conversation:focus,
.conversation:active {
  background-color: var(--header-background);
}
.conversation[theme="dark"] .nameUser {
  color: rgb(255, 255, 255);
}
.conversation[theme="dark"]:hover,
.conversation[theme="dark"]:focus,
.conversation[theme="dark"]:active {
  background-color: var(--dark-header);
}
.newMessage {
  background: var(--green-color);
  font-size: 13px;
  min-width: 30px;
  min-height: 30px;
  right: 10px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
