export default {
  KEY_API: "dc8ebe71dbe5c4dda7c2d96cef63946677c426cb",
  API: "https://emoji-api.com/emojis",
  async emojisList() {
    try {
      const api = `${this.API}?access_key=${this.KEY_API}`;
      const response = await fetch(api);
      const emojiData = await response.json();
      return emojiData;
    } catch (err) {
      console.error(err);
    }
  },
  async searchEmojiList(typeEmoji) {
    try {
      const api = `${this.API}?search=${typeEmoji}&access_key=${this.KEY_API}`;
      const response = await fetch(api);
      const emojiData = await response.json();
      return emojiData;
    } catch (err) {
      console.error(err);
    }
  },
};
