<template>
  <ModelPopup :id="id" :title="title">
    <template #footer>
      <button
        class="btn px-4 fw-semibold rounded-5 border-light-subtle"
        style="color: var(--green-header); background: white; font-size: 14px"
        data-bs-dismiss="modal"
      >
        Don't
      </button>
      <button
        @click="deleteMessageFirebase"
        class="btn px-4 fw-semibold rounded-5 text-white"
        style="background: var(--green-header); font-size: 14px"
        data-bs-dismiss="modal"
      >
        delete
      </button></template
    >
  </ModelPopup>
</template>

<script>
import ModelPopup from "@/components/ModePopup.vue";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
import { mapState } from "vuex";
export default {
  name: "modelDeleteMessage",
  props: {
    id: String,
    title: String,
  },
  methods: {
    getRepositoryMessage() {
      return makeDatabaseMessagesRepository(
        this.profile.id,
        this.detailsChat.idChat
      );
    },
    deleteMessageFirebase() {
      this.getRepositoryMessage().deleteDataByid(this.idMessageDelete);
    },
  },
  computed: {
    ...mapState(["idMessageDelete", "profile", "detailsChat"]),
  },
  components: { ModelPopup },
};
</script>

<style scoped>
button:hover {
  box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.274);
}
</style>
