<template>
  <CollapseConversation
    v-if="this.conversations != null"
    :id="'collapseDetailsUser'"
  >
    <div class="card card-body rounded-0 overflow-y-auto">
      <div class="text-center border-bottom border-2">
        <img
          v-if="conversations.imgUser"
          data-bs-toggle="modal"
          data-bs-target="#friend-s-photo"
          class="rounded-circle"
          style="
            width: 180px;
            height: 180px;
            object-fit: cover;
            cursor: pointer;
          "
          :src="conversations.imgUser"
        />
        <i
          v-else
          class="fa-solid fa-user mt-5 mb-5 text-secondary"
          style="font-size: 150px"
        ></i>
        <h4 style="user-select: none" class="text-capitalize fw-normal pt-2">
          {{ conversations.name }}
        </h4>
      </div>
      <span style="font-size: 15px; color: var(--matte-font-color)">About</span>
      <h5
        class="mb-5"
        style="user-select: none; cursor: pointer"
        @click="copyContent(conversations.about)"
      >
        {{ conversations.about }}
      </h5>
      <button
        class="w-100 btn btn-success"
        @click="getListMessagDistintve()"
        data-bs-toggle="modal"
        data-bs-target="#distinctive-messages"
      >
        <i class="fa-regular fa-star"></i> Distinctive messages
      </button>
      <Transition name="bounce" class="text-center w-100">
        <span
          v-if="copy"
          class="position-absolute start-0 top-0 text-white fs-4"
          style="background: rgba(0, 0, 0, 0.336)"
          >copy</span
        >
      </Transition>
      <button
        class="btn btn-danger w-100 mt-4 rounded-2 text-white sticky-bottom start-0 text-center"
        data-bs-toggle="modal"
        data-bs-target="#block-the-user"
      >
        <span v-if="!detailsChat.block">block</span>
        <span v-else>unblock</span>
      </button>
    </div>
  </CollapseConversation>
  <CollapseConversation
    v-if="this.conversations != null"
    :id="'collapseSearchMessages'"
  >
    <SearchMessagesChat />
  </CollapseConversation>
  <ModePopup
    v-if="this.conversations != null"
    :id="'block-the-user'"
    :title="'Block the user'"
  >
    <template #body>
      <p v-if="!detailsChat.block" class="text-break">
        Do you want to block the user
        <i class="fa-regular fa-hand-point-right text-danger"></i>
        {{ conversations.name }} ?
      </p>
      <p v-else class="text-break">
        Do you want to unblock the user?
        <i class="fa-regular fa-hand-point-right text-danger"></i>
        {{ conversations.name }} ?
      </p>
    </template>
    <template #footer>
      <button class="btn btn-outline-success" data-bs-dismiss="modal">
        close
      </button>
      <button
        @click="onBlock(!detailsChat.block)"
        class="btn btn-outline-danger"
        data-bs-dismiss="modal"
      >
        <span v-if="!detailsChat.block">Block</span>
        <span v-else>unblock</span>
      </button>
    </template>
  </ModePopup>
  <ModePopup
    v-if="this.conversations != null"
    :id="'friend-s-photo'"
    :title="'Friend`s photo'"
  >
    <template #body>
      <img
        class="w-100 h-100"
        style="object-fit: cover"
        :src="conversations.imgUser"
        alt=""
      />
    </template>
    <template #footer>
      <button class="btn btn-outline-success" data-bs-dismiss="modal">
        close
      </button>
      <a
        :href="conversations.imgUser"
        :Download="`whatsAppIbrahim-${conversations.name}`"
      >
        <span
          class="btn rounded text-white"
          style="background: var(--green-color)"
        >
          <i class="fa-solid fa-download fs-4"></i>
        </span>
      </a>
    </template>
  </ModePopup>
  <ModePopup
    v-if="this.conversations != null"
    :id="'distinctive-messages'"
    :title="'Distinctive messages'"
  >
    <template #body>
      <ListMessages :ViewAll="true" :arrayMessage="list" />
    </template>
    <template #footer>
      <button class="btn btn-outline-success" data-bs-dismiss="modal">
        close
      </button>
    </template>
  </ModePopup>
</template>

<script>
import { mapState } from "vuex";
import ModePopup from "../ModePopup.vue";
import CollapseConversation from "./CollapseConversation.vue";
import SearchMessagesChat from "./SearchMessagesChat.vue";
import ListMessages from "./ListMessages.vue";
import { makeDatabaseChatsRepository } from "@/firebase/database-chats-repository";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
export default {
  name: "userDetails",
  data() {
    return {
      copy: false,
      list: [],
      block: false,
      heBlock: false,
    };
  },
  computed: {
    ...mapState(["conversations", "detailsChat", "profile"]),
  },
  methods: {
    async getListMessagDistintve() {
      makeDatabaseMessagesRepository(
        this.profile.id,
        this.detailsChat.idChat
      ).snapshotDataMessagesDistintve((list) => {
        this.list = list;
      });
    },
    async onBlock(block) {
      this.detailsChat.block = block;
      makeDatabaseChatsRepository(this.profile.id).updateById(
        this.detailsChat.idChat,
        { block: block }
      );
      makeDatabaseChatsRepository(this.detailsChat.idFrinde).updateById(
        this.detailsChat.idChatFrinde,
        { heBlock: block }
      );
    },
    async copyContent(text) {
      try {
        await navigator.clipboard.writeText(text);
        this.copy = true;
        this.shwoCopy();
      } catch (err) {
        console.error("Failed to copy: ", err);
      }
    },
    shwoCopy() {
      setTimeout(() => (this.copy = false), 2000);
    },
  },
  components: {
    CollapseConversation,
    SearchMessagesChat,
    ModePopup,
    ListMessages,
  },
};
</script>

<style scoped>
.card {
  display: none;
  background: fixed;
  color: var(--matte-font-color);
  height: inherit;
}
.show .card {
  translate: 0px 0px;
  display: block;
  animation-name: aa;
  animation-duration: 0.6s;
}
@keyframes aa {
  0% {
    translate: 0px -100px;
    opacity: 0;
  }
  50% {
    translate: 0px -100px;
    opacity: 0;
  }
  100% {
    translate: 0px 0px;
    opacity: 1;
  }
}
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
</style>
