<template>
  <div class="collapse" id="collapseEmoji">
    <SearchContainer>
      <template #inputSearch>
        <input
          v-model="search"
          :theme="theme"
          placeholder="Search Emoji"
          type="search"
          class="form-control border-0 ps-5 p-2"
          @keyup="handleKeyUp"
        />
      </template>
    </SearchContainer>
    <div
      :theme="theme"
      class="overflow-y-auto pt-1 d-flex flex-wrap align-items-center justify-content-center w-100 overflow-x-hidden"
      style="height: 250px; user-select: none"
    >
      <div
        v-for="(emoji, index) in listEmoji"
        :key="index"
        class="text-center"
        style="cursor: pointer; min-width: 40px; min-height: 40px"
      >
        <span
          draggable="false"
          class="fs-3 text-center m-1 rounded"
          @click="sendEmoji(index)"
          :emoji-name="emoji.slug"
          >{{ emoji.character }}</span
        >
      </div>
    </div>
  </div>
</template>
//
<script>
import apiEmoji from "@/emojiApi/index.js";
import SearchContainer from "./SearchContainer.vue";
export default {
  name: "emojiComponent",
  data() {
    return {
      listEmoji: [],
      search: "",
      listEmojiSend: [],
      typingTimer: null,
    };
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
  },
  methods: {
    async getListEmoji() {
      const list = await apiEmoji.emojisList();
      this.listEmoji = list;
    },
    handleKeyUp() {
      clearTimeout(this.typingTimer);
      this.typingTimer = setTimeout(this.doneTyping, 300);
    },
    async doneTyping() {
       this.listEmoji = await apiEmoji.searchEmojiList(this.search);
    },
    sendEmoji(index) {
      this.listEmojiSend.push(this.listEmoji[index].character);
      this.$emit("onEmoji", this.listEmojiSend);
    },
  },
  mounted() {
    this.getListEmoji();
  },
  components: { SearchContainer },
};
</script>

<style scoped>
span:hover {
  background: rgba(171, 171, 171, 0.638);
}
.flex-wrap {
  background: var(--app-background);
}
.flex-wrap[theme="dark"] {
  background: var(--dark-header);
}
.flex-wrap[theme="dark"]::-webkit-scrollbar-thumb {
  background: var(--dark-header);
}
.flex-wrap[theme="dark"]::-webkit-scrollbar {
  background-color: var(--dark-background);
}
.form-control[theme="dark"] {
  background-color: var(--dark-header);
}
.form-control::placeholder {
  color: var(--matte-font-color);
}
.form-control:focus {
  box-shadow: none;
}
</style>
@/emojiApi/index.js
