<template>
  <div
    :theme="theme"
    class="card p-0 rounded-3 card-body h-100 rounded-0 overflow-y-auto"
    style="background: fixed; color: var(--matte-font-color)"
  >
    <SearchContainer>
      <template #inputSearch>
        <input
          v-model="query"
          :theme="theme"
          type="search"
          class="form-control ps-5 p-2"
          :placeholder="placeholder"
        />
      </template>
    </SearchContainer>

    <TransitionGroup name="list" class="list w-100 m-0 p-0" tag="ul">
      <li
        class="w-100"
        v-for="(message, index) in messagesList"
        :key="index"
        style="list-style-type: none"
        :class="{
          'text-end': message.send,
        }"
      >
        <SincleMessage
          :dataMaessage="message"
          @indexMessageDelete="indexMessageDelet"
        />
      </li>
    </TransitionGroup>
    <!-- </div> -->
  </div>
</template>

<script>
import SearchContainer from "../conversations/SearchContainer.vue";
import SincleMessage from "../conversations/SincleMessage.vue";
export default {
  props: {
    arrayMessage: Array,
    ViewAll: Boolean,
  },
  data() {
    return {
      arraySearchMessage: [],
      query: "",
      placeholder: "Search messages",
    };
  },
  components: {
    SincleMessage,
    SearchContainer,
  },
  methods: {},
  computed: {
    theme() {
      return this.$store.state.theme;
    },
    messagesList() {
      if (this.query != "")
        return this.arrayMessage.filter(({ message }) =>
          message.toLowerCase().includes(this.query)
        );
      else if (this.ViewAll) return this.arrayMessage;
      return [];
    },
  },
};
</script>

<style>
.form-control {
  border: none;
  padding-left: 40px;
  color: var(--matte-font-color);
  background-color: var(--header-background);
}
.form-control:focus {
  box-shadow: none;
}
.card {
  display: none;
}
.show .card {
  translate: 0px 0px;
  display: block;
  animation-name: aa;
  animation-duration: 0.6s;
}
@keyframes aa {
  0% {
    translate: 0px -100px;
    opacity: 0;
  }
  50% {
    translate: 0px -100px;
    opacity: 0.5;
  }
  100% {
    translate: 0px 0px;
    opacity: 1;
  }
}
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
.form-control {
  border: none;
  color: var(--matte-font-color);
  background-color: var(--header-background);
}
.form-control[theme="dark"] {
  background-color: var(--dark-header);
}
.form-control::placeholder {
  color: var(--matte-font-color);
}
.card[theme="dark"]::-webkit-scrollbar-thumb {
  background: var(--dark-header);
}
.card[theme="dark"]::-webkit-scrollbar {
  background-color: var(--dark-background);
}
input:focus.form-control::placeholder {
  opacity: 0;
}
</style>
