<template>
  <button
    :ref="itemsMessage"
    class="itemMessage dropdown-item p-2 me-1 text-capitalize"
  >
    {{ itemsMessage }}
  </button>
</template>

<script>
export default {
  name: "itemMessageDropdown",
  props: {
    itemsMessage: String,
  },
  mounted() {
    if (this.itemsMessage == "delete") {
      this.$refs.delete.setAttribute("data-bs-toggle", "modal");
      this.$refs.delete.setAttribute("data-bs-target", "#exampleModalDeletMessage")
    }
  },
};
</script>

<style scoped>
button:hover{
  background:fixed;
}
</style>