<template>
  <form
    @submit.prevent="addMessageUser"
    style="background: none"
    class="d-flex p-0 align-items-center w-100 p-0 m-0"
    v-if="!detailsChat.block && !detailsChat.heBlock"
  >
    <button
      type="button"
      data-bs-toggle="collapse"
      data-bs-target="#collapseEmoji"
      aria-expanded="false"
      aria-controls="collapseEmoji"
      class="btn h-100 fs-2 border-0"
    >
      <i class="fa-regular fa-face-laugh"></i>
    </button>
    <button type="button" class="btn paperclip ms-1 h-100 me-1 fs-4 border-0">
      <i class="fa-solid fa-paperclip" style="transition: 0.3s"></i>
    </button>
    <textarea
      :theme="theme"
      style="max-height: 45px"
      class="form-control m-auto h-auto p-2"
      id="inputMessage"
      :placeholder="placeholder"
      v-model="message"
      @focusin="closeEmoji()"
      ref="message"
      @keyup.enter="addMessageUser"
    />
    <button class="btn ms-1 h-100 fs-4 border-0" type="submit">
      <i class="fa-regular fa-paper-plane"></i>
    </button>
  </form>
  <div class="badge text-bg-danger" v-else-if="detailsChat.block">
    The user side is blocked
  </div>
  <div class="badge text-bg-danger" v-else-if="detailsChat.heBlock">
    You cannot message this person
  </div>
</template>
<script>
import { mapState } from "vuex";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
import { makeDatabaseChatsRepository } from "@/firebase/database-chats-repository";
export default {
  name: "inputText",
  props: {
    placeholder: String,
    emoji: Array,
  },
  data() {
    return {
      message: "",
      blockUser: null,
    };
  },
  methods: {
    setDataChat(data) {
      this.$store.commit("setDataChat", data);
    },
    getRepositoryMessage() {
      return makeDatabaseMessagesRepository(
        this.profile.id,
        this.detailsChat.idChat
      );
    },
    getRepositoryMessageFrinde() {
      return makeDatabaseMessagesRepository(
        this.detailsChat.idFrinde,
        this.detailsChat.idChatFrinde
      );
    },
    getRepositoryChats() {
      return makeDatabaseChatsRepository(this.profile.id);
    },
    getRepositoryChatsFrind() {
      return makeDatabaseChatsRepository(this.conversations.id);
    },

    async VerifyChatAndSender(message) {
      const oldChat = await this.getRepositoryChats().checkChatExist(
        this.conversations.id
      );
      const oldChatFrind = await this.getRepositoryChatsFrind().checkChatExist(
        this.profile.id
      );
      if (!oldChat && !oldChatFrind) {
        const dataChat = await this.getRepositoryChats().addChat(
          this.conversations.id
        );
        const dataChatFrind = await this.getRepositoryChatsFrind().addChat(
          this.profile.id,
          dataChat.idChat
        );
        await this.getRepositoryChats().updateById(dataChat.idChat, {
          idChatFrinde: dataChatFrind.idChat,
        });
        this.setDataChat({ ...dataChat, idChatFrinde: dataChatFrind.idChat });
      }
      this.sendMessage(message);
    },
    // TODO: function update to functions
    addMessageUser() {
      if (this.message.trim().length != 0) {
        this.$refs.message.focus();
        this.VerifyChatAndSender(this.message);
        this.message = "";
        this.updateingTimeChat();
      }
    },
    handlerSend() {
      this.closeEmoji();
      this.audioMessage();
    },
    closeEmoji() {
      document.getElementById("collapseEmoji").classList.remove("show");
    },
    audioMessage() {
      const audio = new Audio(
        "https://audio-previews.elements.envatousercontent.com/files/105394911/preview.mp3?response-content-disposition=attachment%3B+filename%3D%22QKTA234-pop.mp3%22"
      );
      audio.play();
    },
    addEmojiInComment() {
      this.message = `${this.message}${this.emoji[this.emoji.length - 1]}`;
    },
    async sendMessage(message) {
      if (!this.replayMessage) {
        const idMessage = await this.getRepositoryMessage().sendAndAddMessage(
          message,
          true,
          ""
        );
        const idMessageFrind =
          await this.getRepositoryMessageFrinde().sendAndAddMessage(
            message,
            false,
            idMessage
          );
        this.getRepositoryMessage().updateById(idMessage, {
          otherٍSideId: idMessageFrind,
        });
      } else {
        const idMessage =
          await this.getRepositoryMessage().sendAndAddMessageReplay(
            message,
            true,
            "",
            this.replayMessage
          );
        this.replayMessage.name = this.replayMessage.send
          ? this.profile.name
          : "you";
        const idMessageFrind =
          await this.getRepositoryMessageFrinde().sendAndAddMessageReplay(
            message,
            false,
            idMessage,
            this.replayMessage
          );
        this.getRepositoryMessage().updateById(idMessage, {
          otherٍSideId: idMessageFrind,
        });
        this.$store.commit("setDataReplayMessage", null);
      }

      this.handlerSend();
    },
    async updateingTimeChat() {
      try {
        this.getRepositoryChats().updateById(this.detailsChat.idChat, {
          lastTime: new Date().getTime(),
        });
        this.getRepositoryChatsFrind().updateById(
          this.detailsChat.idChatFrinde,
          {
            lastTime: new Date().getTime(),
          }
        );
      } catch (err) {
        console.error(err.message);
      }
    },
  },
  mounted() {
    // this.getRepositoryChats().snapshotDataOneById(
    //   this.detalisChat.idChat,
    //   (data) => {}
    // );
  },
  computed: {
    ...mapState([
      "profile",
      "theme",
      "conversations",
      "detailsChat",
      "replayMessage",
    ]),

    emojis() {
      if (this.emoji) {
        return this.emoji.join("");
      }
    },
  },
  watch: {
    emojis: {
      handler() {
        this.addEmojiInComment();
      },
    },
  },
};
</script>

<style scoped>
.form-control:focus {
  box-shadow: none;
}
.btn:hover {
  background: none;
  color: var(--matte-font-color);
}
.btn:active {
  background-color: #a9a9a9a7;
  color: white;
}
button {
  color: var(--matte-font-color);
}
::-webkit-scrollbar {
  border-radius: 10px;
  background: fixed;
}

::-webkit-scrollbar-thumb {
  background: var(--matte-font-color);
  border-radius: 10px;
}

.form-control {
  height: 3rem;
  resize: none;
  display: block;
  user-select: text;
  white-space: pre-wrap;
  word-break: break-word;
  background: white;
}
.form-control[theme="dark"] {
  background-color: var(--dark-header);
  border: none;
  color: var(--matte-font-color);
}
.form-control::placeholder {
  color: var(--matte-font-color);
}
.paperclip:focus.paperclip i {
  transform: rotate(135deg);
}
</style>
