<template>
  <div
    :theme="theme"
    class="offcanvas p-0 m-xl-4 offcanvas-start"
    data-bs-scroll="true"
    data-bs-backdrop="false"
    :id="id"
    :aria-labelledby="`${id}Label`"
  >
    <div
      class="offcanvas-header text-capitalize d-flex align-items-end p-0 pb-2 px-1"
    >
      <h5 class="offcanvas-title text-white" :id="`${id}Label`">
        <button
          class="btn border-0 text-white"
          data-bs-dismiss="offcanvas" aria-label="Close"
        >
          <span class="fs-4"><i :class="classIcon"></i></span>
        </button>
        <span class="ms-4 pt-5">{{ title }}</span>
      </h5>
    </div>
    <div class="offcanvas-body p-0">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "offcanvsNewChat",
  props: {
    id: String,
    title: String,
    classIcon: String,
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
  },
};
</script>

<style scoped>
.offcanvas-header {
  background-color: var(--green-header);
  min-height: 103px;
}
.offcanvas[theme="dark"] .offcanvas-header {
  background-color: var(--dark-header);
}
.offcanvas[theme="dark"] .offcanvas-body {
  background-color: var(--dark-background);
}
.offcanvas[theme="dark"] .offcanvas-body::-webkit-scrollbar-thumb {
  background:var(--dark-header);
}
.offcanvas.offcanvas-start{
  width:inherit
}
.offcanvas[theme="dark"] .offcanvas-body::-webkit-scrollbar {
  background-color: var(--dark-background);
}
@media (max-width: 1200px) {
  .offcanvas {
    height: 100dvh;
  }
}
@media (min-width: 1200px) {
  .offcanvas {
    height: calc(100dvh - 48px);
  }
}

</style>
