<template>
  <div
    :id="dataMaessage.id"
    class="message position-relative d-inline-block p-1 rounded"
    :class="[
      classMessage,
      {
        interaction:
          dataMaessage.interaction.he || dataMaessage.interaction.you,
      },
    ]"
    :theme="theme"
  >
    <i class="fa-solid fa-play fa-rotate-90 position-absolute"></i>
    <a
      :href="`#${dataMaessage.replayMessage.id}`"
      class="btn replay w-100 p-1 rounded-2 text-start"
      style="border-left: var(--green-header) solid; cursor: pointer"
      v-if="dataMaessage.replayMessage"
    >
      <div style="font-size: 10px">{{ dataMaessage.replayMessage.name }}</div>
      <div class="text-break">{{ dataMaessage.replayMessage.message }}</div>
    </a>
    <p v-if="!link" :class="{ singleEmoji: single }" class="m-0 text-break">
      {{ dataMaessage.message }}
    </p>
    <a
      v-else
      class="link-opacity-100 text-break"
      target="_blank"
      :href="dataMaessage.message"
      >{{ dataMaessage.message }}</a
    >
    <div class="textTime text-end p-0 pt-1 text-lowercase">
      <i v-if="dataMaessage.star" class="fa-solid fa-star"></i>
      {{ typeTime }}
      <i
        :class="{ view: dataMaessage.viewMessage }"
        v-if="dataMaessage.send"
        class="fa-solid fa-check ms-1"
      ></i>
    </div>
    <span
      data-bs-toggle="dropdown"
      aria-expanded="false"
      class="arrow position-absolute rounded-circle me-1 end-0 top-0"
      ><i class="fa-solid fa-angle-down"></i>
    </span>
    <ul
      id="listOfMessageDetails"
      class="dropdown-menu rounded-0 z-3"
      style="box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.134); border: none"
    >
      <li v-for="(item, index) in listItemMessage" :key="index">
        <ItemMessageDropdown
          :itemsMessage="item"
          @click="clickItemMessage(item)"
        />
      </li>
    </ul>
    <div>
      <div
        class="smile position-absolute top-0 ms-2 h-100 d-flex align-items-center fs-6"
      >
        <span
          class="smileSpan rounded-circle fs-6"
          style="
            cursor: pointer;

            background: rgba(0, 0, 0, 0.326);
            color: rgba(255, 255, 255, 0.967);
          "
          data-bs-toggle="dropdown"
          aria-expanded="false"
          ><i class="fa-solid fa-face-smile p-2 rounded-circle"></i
        ></span>
        <ul
          class="smileList border-0 shadow px-1 rounded-5 overflow-hidden position-absolute dropdown-menu"
        >
          <li
            v-for="(emoji, index) in listEmoji"
            :key="index"
            class="fs-4"
            style="cursor: pointer"
            @click="clickhandlarEmoji(emoji)"
          >
            <ItmeListEmojiMessageDropdown :emoji="emoji" />
          </li>
        </ul>
      </div>
    </div>
    <Transition name="bounce">
      <div
        class="interactionEmoji position-absolute"
        style="font-size: 15px; bottom: -14px; right: 0px; user-select: none"
        v-if="dataMaessage.interaction.he || dataMaessage.interaction.you"
      >
        <span
          style="background: rgba(255, 255, 255, 0.551)"
          class="rounded-start"
          v-if="dataMaessage.interaction.he"
        >
          {{ dataMaessage.interaction.he }} </span
        ><span style="background: #54656f89" class="rounded-end"
          >{{ dataMaessage.interaction.you }}
        </span>
      </div>
    </Transition>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
import ItemMessageDropdown from "./ItemMessageDropdown.vue";
import ItmeListEmojiMessageDropdown from "./ItmeListEmojiMessageDropdown.vue";
export default {
  name: "sincleMessage",
  data() {
    return {
      listEmoji: ["👍", "❤️", "😂", "😢", "😮", "🙏"],
      listItemMessage: ["replay", "forward", "star", "delete"],
      interaction: false,
      link: false,
    };
  },
  props: {
    dataMaessage: {
      message: String,
      interaction: String,
      send: Boolean,
      timeM: Number,
      id: String,
      otherٍSideId: String,
      viewMessage: Boolean,
      star: Boolean,
    },
  },
  components: {
    ItmeListEmojiMessageDropdown,
    ItemMessageDropdown,
  },
  computed: {
    typeTime() {
      return new Date(this.dataMaessage.timeM).toLocaleTimeString([], {
        hour: "2-digit",
        minute: "2-digit",
      });
    },
    classMessage() {
      return this.dataMaessage.send ? `iMaessage` : `youMaessage`;
    },
    single() {
      return (
        this.dataMaessage.message.trim().length <= 2 &&
        this.dataMaessage.message.trim().includes("\ud83d")
      );
    },
    ...mapState(["theme", "detailsChat", "profile", "conversations"]),
  },
  methods: {
    getRepositoryMessage() {
      return makeDatabaseMessagesRepository(
        this.profile.id,
        this.detailsChat.idChat
      );
    },
    getRepositoryMessageFrinde() {
      return makeDatabaseMessagesRepository(
        this.detailsChat.idFrinde,
        this.detailsChat.idChatFrinde
      );
    },
    clickhandlarEmoji(emoji) {
      this.dataMaessage.interaction.you =
        this.dataMaessage.interaction.you != emoji
          ? emoji
          : (this.dataMaessage.interaction.you = "");
      this.getRepositoryMessage().updateById(this.dataMaessage.id, {
        interaction: this.dataMaessage.interaction,
      });
      if (!this.detailsChat.block && !this.detailsChat.heBlock)
        this.getRepositoryMessageFrinde().updateById(
          this.dataMaessage.otherٍSideId,
          {
            interaction: {
              he: this.dataMaessage.interaction.you,
              you: this.dataMaessage.interaction.he,
            },
          }
        );
    },
    clickItemMessage(item) {
      switch (item) {
        case "delete":
          this.messageDelete();
          break;
        case "replay":
          this.replayMessage();
          break;
        case "star":
          this.starMessage(!this.dataMaessage.star);
          break;
      }
    },
    messageDelete() {
      this.$store.commit("setIdMessageDelete", this.dataMaessage.id);
    },
    replayMessage() {
      this.$store.commit("setDataReplayMessage", {
        message: this.dataMaessage.message,
        name: this.dataMaessage.send ? "you" : this.conversations.name,
        id: this.dataMaessage.id,
      });
    },
    starMessage(star) {
      this.getRepositoryMessage().updateById(this.dataMaessage.id, {
        star: star,
      });
    },
    isLink(text) {
      // Regular expression to match URLs
      var urlRegex = /(https?:\/\/[^\s]+)/;
      // Test if the text matches the URL pattern
      return urlRegex.test(text);
    },
  },
  mounted() {
    this.dataMaessage.interaction != undefined
      ? (this.interaction = true)
      : (this.interaction = false);
    if (this.isLink(this.dataMaessage.message)) this.link = true;
  },
};
// en
</script>

<style scoped>
.youMaessage,
.iMaessage {
  margin-bottom: 7px;
}
.interaction {
  margin-bottom: 20px;
}
.view {
  color: blue;
  text-shadow: 0px 0px 2px blue;
}
.replay {
  background: rgba(176, 199, 146, 0.74);
  border: none;
}

.message[theme="dark"] .replay {
  background: rgba(57, 119, 155, 0.621);
}
.youMaessage {
  background: rgb(255, 255, 255);
  max-width: 65%;
  margin-right: auto;
  margin-left: 20px;
}
.message[theme="dark"].youMaessage {
  background: var(--dark-header);
  color: white;
}
.youMaessage .fa-play {
  top: -2px;
  left: -4px;
  color: rgb(255, 255, 255);
}
.message[theme="dark"].youMaessage .fa-play {
  color: var(--dark-header);
}
.message[theme="dark"] #listOfMessageDetails {
  background: var(--dark-header);
}
.message[theme="dark"] #listOfMessageDetails li:hover {
  background: var(--dark-background);
}
.arrow {
  opacity: 0;
  transition: 0.2s;
  cursor: pointer;
  color: var(--matte-font-color);
}
.youMaessage .smile {
  transition: 0.2s;
  left: 100%;
}
.smileSpan {
  opacity: 0;
}
.textTime {
  color: var(--matte-font-color);
  font-size: 11px;
}
.youMaessage:hover .smileSpan {
  opacity: 1;
}
.iMaessage:hover .smileSpan {
  opacity: 1;
}
.youMaessage:hover .arrow {
  opacity: 1;
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
  background: rgb(255, 255, 255);
  color: var(--matte-font-color);
}
.message[theme="dark"].youMaessage:hover .arrow {
  opacity: 1;
  box-shadow: 0px 0px 4px 1px var(--dark-header);
  background: var(--dark-header);
}
.iMaessage {
  background: rgb(198, 239, 144);
  max-width: 65%;
  margin-left: auto;
  margin-right: 20px;
}
.iMaessage .fa-play {
  top: -2px;
  right: -4px;
  color: rgb(198, 239, 144);
}
.iMaessage:hover .arrow {
  opacity: 1;
  background: rgb(198, 239, 144);
  box-shadow: 0px 0px 4px 1px rgb(198, 239, 144);
  color: var(--matte-font-color);
}
.iMaessage .smile {
  transition: 0.2s;
  right: 100%;
  margin-right: 3px;
}
.iMaessage:hover .smile {
  opacity: 1;
}
.singleEmoji {
  width: 80px;
  height: 55px;
  font-size: 42px;
}
.itemMessage:active {
  background-color: var(--app-background);
}
.show .itemMessage {
  animation-name: list;
  animation-duration: 0.4s;
  color: var(--matte-font-color);
  font-size: 15px;
}
.smileList.show {
  user-select: none;
  display: flex;
  animation-name: list;
  animation-duration: 0.4s;
}
.message[theme="dark"] .smileList.show {
  background: var(--dark-header);
}
@keyframes list {
  0% {
    translate: 100px;
    opacity: 0;
  }
  100% {
    translate: 0px;
    opacity: 1;
  }
}
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(2);
  }
  100% {
    transform: scale(1);
  }
}
</style>
