<template>
  <button @click="onFollScreen" class="btn position-absolute">
    <i
      v-if="!fullscreen"
      class="fa-solid fa-up-right-and-down-left-from-center"
    ></i>
    <i v-else class="fa-solid fa-down-left-and-up-right-to-center"></i>
  </button>
</template>

<script>
export default {
  name: "goFullscreenButton",
  data() {
    return {
      fullscreen: false,
    };
  },
  methods: {
    onFollScreen() {
      if (!this.fullscreen) {
        this.fullscreen = true;
        document.documentElement.requestFullscreen();
      } else {
        this.fullscreen = false;
        document.exitFullscreen();
      }
    },
  },
};
</script>

<style scoped>
.btn,
.btn:active {
  color: #fff;
  margin: 5px;
  bottom:61px;
  font-size: 12px;
  left: 0%;
  background: var(--green-color);
  opacity: 0.5;
  z-index: 111;
}
</style>
