<template>
  <header
    :theme="theme"
    class="text-center z-3 position-relative d-flex align-items-center"
  >
    <LeftSection
      class="h-100"
      style="border-end: solid var(--matte-font-color) 1.5px"
    >
      <span
        class="p-0 border-0 mx-2 text-center me-auto rounded-circle"
        style="cursor: pointer"
      >
        <UserImg
          data-bs-toggle="offcanvas"
          data-bs-target="#offcanvasProfile"
          aria-controls="offcanvasProfile"
          :srcU="profile.imgUser"
        />
      </span>
      <button
        data-bs-toggle="offcanvas"
        data-bs-target="#offcanvasNewChat"
        aria-controls="offcanvasNewChat"
        class="icon btn p-0 me-1 rounded-circle"
      >
        <svg
          viewBox="0 0 24 24"
          height="24"
          width="24"
          preserveAspectRatio="xMidYMid meet"
          class=""
          fill="none"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M5.16667 3.75C3.69391 3.75 2.5 4.94391 2.5 6.41667V17.5833C2.5 19.0561 3.69391 20.25 5.16667 20.25H18.8333C20.3061 20.25 21.5 19.0561 21.5 17.5833V8.75L23.7458 5.29499C24.1782 4.62974 23.7008 3.75 22.9073 3.75H5.16667ZM14.9672 12.9911H12.9914V14.9671C12.9914 15.3999 12.7366 15.8175 12.3238 15.9488C11.6391 16.1661 11.009 15.6613 11.009 15.009V12.9911H9.03279C8.59949 12.9911 8.1819 12.7358 8.05099 12.3226C7.83412 11.6381 8.33942 11.0089 8.99134 11.0089H11.009V9.03332C11.009 8.60007 11.2639 8.18252 11.6767 8.05119C12.3609 7.83391 12.9914 8.33872 12.9914 8.991V11.0089H15.0091C15.6606 11.0089 16.1659 11.6381 15.949 12.3226C15.8185 12.7358 15.4005 12.9911 14.9672 12.9911Z"
            fill="currentColor"
          ></path>
        </svg>
      </button>
      <button
        data-bs-toggle="dropdown"
        aria-expanded="false"
        class="icon btn p-0 me-1 rounded-circle"
      >
        <i class="fa-solid fa-ellipsis-vertical"></i>
      </button>
      <ul class="dropdown-menu rounded-0 bourder-0">
        
        <li v-for="(item, index) in dropdownItems" :key="index">
          <button @click="modeSite(item)" class="dropdown-item text-capitalize">
            <i
              class="fa-regular"
              :class="[item == 'light' ? 'fa-sun' : 'fa-moon']"
            ></i>
            {{ item }} theme
          </button>
        </li>
      </ul>
    </LeftSection>
    <RightSection class="section h-100">
      <RightSection class="w-100" v-if="conversations">
        <UserHeaderVue
          @click="showDetailsUser"
          data-bs-toggle="collapse"
          data-bs-target="#collapseDetailsUser"
          aria-expanded="false"
          aria-controls="collapseDetailsUser"
        />
        <button
          class="icon btn rounded-circle"
          data-bs-toggle="collapse"
          data-bs-target="#collapseSearchMessages"
          aria-expanded="false"
          aria-controls="collapseSearchMessages"
          @click="showSearchMessage"
        >
          <i class="fa-solid fa-magnifying-glass"></i>
        </button>
        <button
          data-bs-toggle="dropdown"
          aria-expanded="false"
          class="modeSite icon btn me-2 rounded-circle"
        >
          <i class="fa-solid fa-ellipsis-vertical"></i>
        </button>
        <ul class="dropdown-menu rounded-0 bourder-0">
          <li v-for="(item, index) in dropdownItemsChat" :key="index">
            <button
              @click="onDropdownItem(item)"
              class="dropdown-item text-capitalize"
              v-if="item != 'Delete chat'"
            >
              {{ item }}
            </button>
            <button
              v-else
              @click="onDropdownItem(item)"
              class="dropdown-item text-capitalize"
              data-bs-toggle="modal"
              data-bs-target="#deleteChatModal"
            >
              {{ item }}
            </button>
          </li>
        </ul></RightSection
      >
    </RightSection>
  </header>
  <ModePopup :id="'deleteChatModal'" :title="'deletion chat'">
    <template #body>
      <p>
        The chat will be deleted
        <span style="color: #b197fc" class="fs-4"
          ><i class="fa-regular fa-trash-can fa-bounce"></i
        ></span>
      </p>
    </template>
    <template #footer>
      <button
        type="button"
        data-bs-dismiss="modal"
        class="btn btn-outline-success"
      >
        Don't
      </button>
      <button
        type="button"
        @click="onDeleteChat"
        data-bs-dismiss="modal"
        class="btn btn-outline-danger"
      >
        delete
      </button>
    </template>
  </ModePopup>
</template>

<script>
import { mapState } from "vuex";
import LeftSection from "../LeftSection.vue";
import RightSection from "../RightSection.vue";
import ModePopup from "../ModePopup.vue";
import UserImg from "./UserImg.vue";
import UserHeaderVue from "./UserFriendHeader.vue";
import { makeDatabaseMessagesRepository } from "@/firebase/database-messages-repository";
export default {
  name: "headerUser",
  components: {
    LeftSection,
    RightSection,
    UserHeaderVue,
    UserImg,
    ModePopup,
  },
  data() {
    return {
      dropdownItems: ["light", "dark"],
      dropdownItemsChat: ["Contact info", "Close chat", "Delete chat"],
    };
  },
  computed: {
    ...mapState(["conversations", "profile", "theme", "detailsChat"]),
  },
  methods: {
    async onDeleteChat() {
      const list = await makeDatabaseMessagesRepository(
        this.profile.id,
        this.detailsChat.idChat
      ).getListMessagesDocs();

      list.forEach((message) => {
        makeDatabaseMessagesRepository(
          this.profile.id,
          this.detailsChat.idChat
        ).deleteDataByid(message.id);
      });
      this.closeChat();
    },
    showSearchMessage() {
      const collapseDetailsUser = document.getElementById(
        "collapseDetailsUser"
      );
      collapseDetailsUser.classList.remove("show");
    },
    showDetailsUser() {
      //TODO:
      const collapseSearchMessages = document.getElementById(
        "collapseSearchMessages"
      );
      collapseSearchMessages.classList.remove("show");
    },
    modeSite(theme) {
      localStorage.setItem("theme", theme);
      this.$store.commit("setTheme", theme);
    },
    closeChat() {
      this.$store.commit("setDataConversations", null);
    },
    onDropdownItem(item) {
      if (item == "Close chat") {
        this.closeChat();
      }
    },
  },
};
</script>

<style scoped>
header {
  height: 60px;
}
header,
header .section {
  background-color: var(--header-background);
}
header[theme="dark"],
header[theme="dark"] .section {
  background-color: var(--dark-header);
}
header[theme="dark"] .dropdown-menu,
header[theme="dark"] .dropdown-menu:hover {
  background-color: var(--dark-header);
  color: white;
}
.icon {
  width: 2.5rem;
  height: 2.5rem;
  border: none;
}
.icon {
  color: var(--icon-color);
}
.icon:active {
  background: #2222222f;
}
.dropdown-item {
  color: var(--icon-color);
}
.dropdown-item:active {
  color: var(--header-background);
  background-color: var(--green-color);
}
</style>
