<template>
  <button
    v-if="conversations"
    class="btn border-0 me-auto h-100 d-flex align-items-center justify-content-center"
  >
    <span class="p-0 fs-5 border-0 mx-2 text-center me-auto rounded-circle">
      <UserImg v-if="conversations.imgUser" :srcU="conversations.imgUser" />
      <i v-else class="fa-solid fa-circle-user fa-2xl"></i>
    </span>
    <div
      class="nameUser ms-3 text-start"
      style="font-size: 11px; color: var(--matte-font-color)"
    >
      <p v-if="conversations.name" class="p-0 m-0 fs-6 text-capitalize">
        {{ conversations.name }}
      </p>
      <p v-if="detailsChat.lastTime" class="p-0 m-0 text-lowercase">
        {{ new Date(this.detailsChat.lastTime).toDateString() }}
        <span class="ms-2 badge bg-primary text-wrap">{{
          new Date(this.detailsChat.lastTime).toLocaleTimeString([], {
            hour: "2-digit",
            minute: "2-digit",
          })
        }}</span>
      </p>
    </div>
  </button>
</template>

<script>
import UserImg from "./UserImg.vue";
export default {
  name: "userHeader",
  components: { UserImg },
  computed: {
    conversations() {
      return this.$store.state.conversations;
    },
    detailsChat() {
      return this.$store.state.detailsChat;
    },
  },
};
</script>
<style scoped>
.btn:active {
  background:none;
}
.rounded-circle {
  animation-name: loding;
  animation-duration: 0.5s;
}
@keyframes loding {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1.1);
  }
}
</style>
