<template>
  <TransitionGroup
    name="list"
    tag="ul"
    class="messages p-0 w-100"
    :theme="theme"
  >
    <li
      style="list-style-type: none"
      v-for="dataMaessage in listMassages"
      :key="dataMaessage.id"
      :class="{
        'text-end': dataMaessage.send,
      }"
    >
      <SincleMessage :dataMaessage="dataMaessage"/>
    </li>
  </TransitionGroup>
  <p id="scroll-of-last-Message" class="text-success text-end fs-3"><i class="fa-regular fa-hand-point-left"></i></p>
  <ModelPopupDelete
    :id="'exampleModalDeletMessage'"
    :title="'Delete message?'"
  />
</template>

<script>
import SincleMessage from "./SincleMessage.vue";
import ModelPopupDelete from "./ModelPopupDelete.vue";
export default {
  name: "messageCompoent",
  data() {
    return {
      time: null,
    };
  },
  props: {
    listMassage: Array,
  },
  components: { SincleMessage, ModelPopupDelete },
  computed: {
    listMassages() {
      return this.$store.state.listMessagesChat;
    },
    lengthListMassages() {
      if (this.listMassages != undefined) return this.listMassages.length;
    },
    theme() {
      return this.$store.state.theme;
    },
  },
  methods: {
    date(number) {
      if (this.time != new Date(number).toDateString()) {
        this.time = new Date(number).toDateString();
        return new Date(number).toDateString();
      }
      return;
    },
  },
  watch: {
    lengthListMassages: {
      immediate: true,
      handler() {
        const div = document.querySelector(".messaging");
        if (div)
          div.scrollTo({
            top: div.scrollHeight - 20,
          });
      },
    },
  },
};
</script>
<style scoped>
.list-enter-active,
.list-leave-active {
  transition: all 0.4s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
</style>
