<template>
  <button
    :theme="theme"
    class="conversation text-start btn border-0 w-100 d-flex align-items-center"
  >
    <UserImg :srcU="user.imgUser" class="ms-2" />
    <div
      class="position-relative mt-auto p-2 d-flex flex-column justify-content-center"
    >
      <span
        v-if="user.name"
        class="nameUser"
        style="text-transform: capitalize"
        >{{ user.name }}</span
      >
      <span v-else style="text-transform: capitalize">not name</span>
      <p style="font-size: 12px; color: var(--matte-font-color)">
        {{ user.email.slice(0, 16) }}
        <span v-if="user.email.length > 16"> ...</span>
      </p>
    </div>
  </button>
</template>

<script>
import UserImg from "../headerMain/UserImg.vue";
export default {
  name: "userNewChat",
  props: {
    user: {
      name: String,
      email: String,
      imgUser: String,
      createdAt: Object,
    },
  },
  computed: {
    theme() {
      return this.$store.state.theme;
    },
  },
  components: {
    UserImg,
  },
};
</script>

