<template>
  <main
    :theme="theme"
    class="home container-fluid position-relative p-xl-4 p-0 w-100 shadow overflow-y-hidden"
  >
    <div
      v-if="profile.id"
      class="w-100 p-0 h-100 overflow-y-hidden position-relative"
      style="box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.221)"
    >
      <article
        class="offcanvasAll position-relative p-0 m-0 col-xxl-4 col-xl-4 col-lg-5 col-md-5 col-sm-7 col-12"
      >
        <ProfileComponent />
        <NewChat />
      </article>
      <HeaderMain class="z-3" />
      <div class="all w-100 p-0 position-relative">
        <div id="twoMainSections" class="w-100 h-100 d-flex">
          <LeftSection
            class="conversations h-100 overflow-x-hidden d-block overflow-y-hidden position-relative"
          >
            <article
              class="conversationsUsers position-relative overflow-x-hidden w-100 text-start overflow-y-auto pt-0 pb-3"
            >
              <ListChatsUser />
              <!-- <p
    class="placeholder-glow position-relative d-flex align-items-center"
  >
    <span
      class="placeholder bg-primary rounded-circle p-4 position-absolute ms-2 start-0"
    ></span>
    <span class="placeholder rounded-3 col-12" style="padding: 40px 0px"></span>
  </p> -->
            </article>
          </LeftSection>
          <RightSection class="rightSection p-0 position-relative">
            <article
              id="chats"
              class="p-0 w-100 h-100 d-flex position-relative flex-column overflow-y-auto overflow-x-hidden"
            >
              <div
                v-if="conversations != null"
                class="messaging pt-5 pb-5 w-100 overflow-y-auto overflow-x-hidden position-relative"
              >
                <MessagesComponent />
              </div>
              <StartWhatsapp v-else />
              <div class="emojis position-sticky w-100" style="bottom: 0px">
                <Transition name="slide-fade">
                  <div
                    v-if="this.replayMessage"
                    class="rplay text-secondary p-2 w-100 p-1"
                    style="user-select: none"
                  >
                    <span
                      style="
                        font-size: 13px;
                        border-left: var(--green-header) solid 5px;
                      "
                      class="name p-1 rounded-3 d-inline-block mb-1"
                      >{{ this.replayMessage.name }}</span
                    >
                    <div
                      class="w-100 d-flex align-items-center justify-content-between"
                    >
                      <span
                        class="p-2 w-100 me-3 rounded-3 text-secondary text-break"
                        >{{ this.replayMessage.message }}</span
                      >
                      <span
                        class="p-2 rounded-3"
                        @click="this.$store.state.replayMessage = null"
                        style="cursor: pointer"
                        ><i class="fa-solid fa-xmark"></i
                      ></span>
                    </div></div
                ></Transition>
                <EmojiComponent @onEmoji="onEmoji" />
              </div>
              <div
                v-if="conversations != null"
                class="textMessage z-3 w-100 p-2 d-flex align-items-center"
              >
                <InputMessage
                  class="px-2 m-auto"
                  style="width: 85%"
                  :placeholder="'Type a message'"
                  :emoji="emoji"
                />
              </div>
            </article>
            <UserDetails />
          </RightSection>
        </div>
      </div>
    </div>
    <LoadingComponent v-else />
    <Transition
      name="bounce"
      style="background: rgba(0, 0, 0, 0.273); height: 100dvh"
      class="errorOfline w-100 position-absolute top-0 start-0 z-3 text-center"
    >
      <div v-if="error" style="width: 200px; height: 200px; background: white">
        <span
          style="width: 270px; height: 130px"
          class="alert alert-danger d-inline-block mt-5"
        >
          <i class="fs-1 fa-solid fa-bomb" style="color: #942424"></i>
          <p class="fs-5">You are not connected to the Internet</p>
        </span>
      </div>
    </Transition>
    <!-- <go-fullscreen-button-vue /> -->
  </main>
</template>

<script>
import { mapState } from "vuex";
import HeaderMain from "@/components/headerMain/HeaderMain.vue";
import StartWhatsapp from "@/components/StartWhatsapp.vue";
import LeftSection from "@/components/LeftSection.vue";
import RightSection from "@/components/RightSection.vue";
import ListChatsUser from "@/components/conversations/ListAnimationChatsUser.vue";
import InputMessage from "@/components/conversations/InputMessage.vue";
import MessagesComponent from "@/components/conversations/MessagesComponent.vue";
import NewChat from "@/components/conversations/NewChat.vue";
import ProfileComponent from "@/components/conversations/ProfileComponent.vue";
import EmojiComponent from "@/components/conversations/EmojiComponent.vue";
import UserDetails from "@/components/detailsConversation/UserDetails.vue";
import LoadingComponent from "@/components/LoadingComponent.vue";
import GoFullscreenButtonVue from '@/components/GoFullscreenButton.vue';
export default {
  name: "HomeView",
  components: {
    HeaderMain,
    StartWhatsapp,
    LeftSection,
    RightSection,
    ListChatsUser,
    InputMessage,
    MessagesComponent,
    NewChat,
    ProfileComponent,
    EmojiComponent,
    UserDetails,
    LoadingComponent,
    GoFullscreenButtonVue
  },
  data() {
    return {
      emoji: [],
      error: false,
    };
  },
  methods: {
    onEmoji(emoji) {
      this.emoji = emoji;
    },
    setTheme(theme) {
      this.$store.commit("setTheme", theme);
    },
  },
  computed: {
    ...mapState(["profile", "conversations", "theme", "replayMessage"]),
  },
  async mounted() {
    addEventListener("offline", () => {
      this.error = true;
    });
    addEventListener("online", () => {
      this.error = false;
    });
    if (!this.profile.email) this.$router.push("/");
    // console.log( await getDataProfile(this.profile.email),this.profile.email)
  },
  // TODO:
  created() {
    this.setTheme(localStorage.theme);
  },
};
</script>

<style scoped>
@import url(../styles/stylesChats.css);
</style>
