<template>
  <div
    class="modal fade"
    :id="id"
    tabindex="-1"
    ref="modal"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    :theme="theme"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content pt-2 pb-2 shadow-lg rounded-0 border-0">
        <div class="modal-header border-0">
          <h1
            class="modal-title fw-normal"
            style="color: var(--matte-font-color); font-size: 14px"
            :id="`${id}Label`"
          >
            {{ title }}
          </h1>
        </div>
        <div class="modal-body">
          <slot name="body"></slot>
        </div>
        <div
          class="modal-footer position-sticky bottom-0"
        >
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "modelPopup",
  props: {
    id: String,
    title: String,
  },
  computed:{
    theme(){
      return this.$store.state.theme
    }
  }
};
</script>

<style scoped>
button:hover {
  box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.274);

}
.modal,.modal-footer{
  background:rgba(255, 255, 255, 0.808)
}
.modal-footer{
  background-color: white;
  border: none;
}
.modal[theme="dark"]{
background:  #202c3395;
}
.modal[theme="dark"] .modal-content,.modal[theme="dark"] .modal-footer{
  background: var(--dark-header);
  color:white
}
</style>
